
*{
  margin: 0px;
  padding: 0px;
}
.containerr {
  background-image: url(img1.jpg);
  height: 600px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #f4f4f4;
}

.desc {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 100px;
}
.desc p{
  overflow: hidden;
}

.center_slide input{
  top: 40%;
  width: 60%;
  height: 30px;
  /* border: border-bottom; */
  /* border-radius: 4px ; */
}

.center_slide button{
  background-color: rgb(108, 251, 222);
  border-radius: 4px;
  margin: 4px;
  padding: 2px;
  text-decoration-style: solid;
}

